.app__form {
    margin-top: 1em;
  }
  
  .form {
    display: flex;
    flex: 1 1 auto;
    &__input {
      width: 45%;
      border: 2px solid #000;
      font-size: 1.3rem;
      padding: .3em;
    }
    &__btn {
      width: 10%;
      font-size: 1.3rem;
      border: 2px solid #000;
      padding: .4em;
      background: inherit;
    }
  }